// src/api/chatService/chat.ts

import { CHATSERVICE_API } from "@/config/env";
import type { ChatRoom, ChatMessage } from "@/types/chatService/chat";

// Í≥µÌÜµ Ïù∏Ï¶ù Ìó§Îçî + userSignId Ìó§Îçî Ìè¨Ìï®
const getHeaders = (userSignId?: string) => {
  const token = localStorage.getItem("accessToken");
  const headers: Record<string, string> = {
    "Content-Type": "application/json",
  };

  if (token) {
    headers["Authorization"] = `Bearer ${token}`;
  }

  if (userSignId) {
    headers["userSignId"] = userSignId; // Î∞±ÏóîÎìú @RequestHeader ÌïÑÏàò!
  }

  return headers;
};

/**
 * Ï±ÑÌåÖÎ∞© ÏÉùÏÑ± (Î∞±ÏóîÎìúÏôÄ Ï†ïÌôïÌûà Îß§Ïπ≠)
 */
export async function createChatRoom(
    participantIds: string[],
    roomName: string,
    currentUserSignId: string
): Promise<ChatRoom> {
  const response = await fetch(`${CHATSERVICE_API}/rooms`, {
    method: "POST",
    headers: getHeaders(currentUserSignId), // userSignId Ìó§Îçî Ìè¨Ìï®
    body: JSON.stringify({
      participantIds,
      roomName: roomName || null, // Î∞±ÏóîÎìúÏóêÏÑú null ÌóàÏö©
    }),
  });

  if (!response.ok) {
    const error = await response.text();
    throw new Error(`Ï±ÑÌåÖÎ∞© ÏÉùÏÑ± Ïã§Ìå®: ${error}`);
  }

  return response.json();
}

/**
 * ÎÇ¥ Ï±ÑÌåÖÎ∞© Î™©Î°ù Ï°∞Ìöå
 */
export async function getChatRooms(userSignId: string): Promise<ChatRoom[]> {
  const response = await fetch(`${CHATSERVICE_API}/rooms`, {
    method: "GET",
    headers: getHeaders(userSignId), // ÌïÑÏàò Ìó§Îçî
  });

  if (response.status === 204) return [];
  if (!response.ok) {
    throw new Error("Ï±ÑÌåÖÎ∞© Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®");
  }

  return response.json();
}

/**
 * Ï±ÑÌåÖ ÌûàÏä§ÌÜ†Î¶¨ Ï°∞Ìöå
 */
export async function getChatHistory(
    roomId: string,
    page = 0,
    size = 50
): Promise<ChatMessage[]> {
  const token = localStorage.getItem("accessToken");
  const headers: Record<string, string> = { "Content-Type": "application/json" };
  if (token) headers["Authorization"] = `Bearer ${token}`;

  const response = await fetch(
      `${CHATSERVICE_API}/rooms/${roomId}/messages?page=${page}&size=${size}`,
      { headers }
  );

  if (response.status === 204) return [];
  if (!response.ok) throw new Error("Î©îÏãúÏßÄ Ï°∞Ìöå Ïã§Ìå®");
  return response.json();
}

/**
 * ÏùΩÏùå Ï≤òÎ¶¨
 */
export async function markMessagesAsRead(
    roomId: string,
    userSignId: string
): Promise<void> {
  const response = await fetch(`${CHATSERVICE_API}/rooms/${roomId}/read`, {
    method: "PUT",
    headers: {
      ...getHeaders(userSignId),
      "Content-Length": "0",
    },
  });

  if (!response.ok) {
    throw new Error("ÏùΩÏùå Ï≤òÎ¶¨ Ïã§Ìå®");
  }
}