"use client";

import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useWebSocket } from '@/hooks/useWebSocket';
import { getChatHistory, markMessagesAsRead } from '@/api/chatService/chat';
import type { ChatMessage, ChatMessageDto } from '@/types/chatService/chat';
import { Send, ArrowLeft } from 'lucide-react';

interface ChatRoomProps {
  roomId: string;
  currentUserId: string;
  currentUserName: string;
  roomName: string;
  onBack: () => void;
}

export default function ChatRoom({
                                   roomId,
                                   currentUserId,
                                   currentUserName,
                                   roomName,
                                   onBack,
                                 }: ChatRoomProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    console.log('=== ChatRoom ÎßàÏö¥Ìä∏ ===');
    console.log('roomId:', roomId);
    console.log('currentUserId:', currentUserId);
    console.log('currentUserName:', currentUserName);
  }, [roomId, currentUserId, currentUserName]);

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  const handleNewMessage = useCallback(
      (newMessage: ChatMessage) => {
        console.log('üì® ÏÉà Î©îÏãúÏßÄ ÏàòÏã†:', newMessage);
        setMessages((prev) => [...prev, newMessage]);
        scrollToBottom();

        if (newMessage.userSignId !== currentUserId) {
          markMessagesAsRead(roomId, currentUserId).catch(console.error);
        }
      },
      [currentUserId, roomId, scrollToBottom]
  );

  // ‚≠ê ÌïµÏã¨ ÏàòÏ†ï: roomIdÎ•º WebSocket ÌõÖÏóê Ï†ÑÎã¨!
  const { isConnected, sendMessage } = useWebSocket({
    url: 'ws://localhost:1005/ws-chat',
    roomId: roomId, // ‚≠ê‚≠ê‚≠ê Ïù¥Í≤å Îπ†Ï†∏ÏûàÏóàÏäµÎãàÎã§!
    onMessage: handleNewMessage,
  });

  useEffect(() => {
    console.log('üîå WebSocket Ïó∞Í≤∞ ÏÉÅÌÉú:', isConnected);
  }, [isConnected]);

  const loadChatHistory = useCallback(async () => {
    setIsLoading(true);
    try {
      console.log('üìö Ï±ÑÌåÖ ÌûàÏä§ÌÜ†Î¶¨ Î°úÎìú:', roomId);
      const history = await getChatHistory(roomId);
      console.log('‚úÖ Î°úÎìúÎêú Î©îÏãúÏßÄ:', history.length, 'Í∞ú');
      setMessages(Array.isArray(history) ? history.reverse() : []);
    } catch (err) {
      console.error('‚ùå ÌûàÏä§ÌÜ†Î¶¨ Î°úÎìú Ïã§Ìå®:', err);
      setMessages([]);
    } finally {
      setIsLoading(false);
      setTimeout(scrollToBottom, 100);
    }
  }, [roomId, scrollToBottom]);

  useEffect(() => {
    loadChatHistory();
  }, [loadChatHistory]);

  const handleSendMessage = useCallback(() => {
    const trimmed = inputMessage.trim();

    console.log('=== Î©îÏãúÏßÄ Ï†ÑÏÜ° ÏãúÎèÑ ===');
    console.log('ÎÇ¥Ïö©:', trimmed);
    console.log('Ïó∞Í≤∞:', isConnected);
    console.log('roomId:', roomId);
    console.log('userId:', currentUserId);

    if (!trimmed) {
      console.log('‚ö†Ô∏è Î©îÏãúÏßÄ ÎπÑÏñ¥ÏûàÏùå');
      return;
    }

    if (!isConnected) {
      console.log('‚ùå WebSocket Ïó∞Í≤∞ ÏïàÎê®');
      alert('ÏÑúÎ≤ÑÏôÄ Ïó∞Í≤∞ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }

    if (!currentUserId) {
      console.log('‚ùå ÏÇ¨Ïö©Ïûê ID ÏóÜÏùå');
      alert('ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.');
      return;
    }

    const messageDto: ChatMessageDto = {
      roomId,
      userSignId: currentUserId,
      name: currentUserName || currentUserId,
      content: trimmed,
      type: 'TALK',
    };

    console.log('üì§ Ï†ÑÏÜ° DTO:', JSON.stringify(messageDto, null, 2));

    try {
      sendMessage(messageDto);
      console.log('‚úÖ sendMessage() ÏôÑÎ£å');
      setInputMessage('');
      inputRef.current?.focus();
    } catch (error) {
      console.error('‚ùå Ï†ÑÏÜ° ÏóêÎü¨:', error);
      alert('Î©îÏãúÏßÄ Ï†ÑÏÜ°Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
    }
  }, [inputMessage, isConnected, currentUserId, currentUserName, roomId, sendMessage]);

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      console.log('‚å®Ô∏è Enter ÌÇ§');
      handleSendMessage();
    }
  };

  const formatTime = (dateString?: string) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleTimeString('ko-KR', {
      hour: '2-digit',
      minute: '2-digit',
      hour12: false,
    });
  };

  return (
      <div className="flex flex-col h-screen bg-gray-50">
        <header className="flex items-center gap-3 px-4 py-3 bg-white border-b border-gray-200 sticky top-0 z-10">
          <button
              onClick={onBack}
              className="p-2 rounded-full hover:bg-gray-100 md:hidden"
              aria-label="Îí§Î°ú"
          >
            <ArrowLeft className="w-5 h-5" />
          </button>
          <h1 className="text-lg font-semibold text-gray-900 truncate max-w-[200px] sm:max-w-none">
            {roomName}
          </h1>
          <div className="ml-auto flex items-center gap-2">
            <span
                className={`w-2 h-2 rounded-full ${
                    isConnected ? 'bg-green-500' : 'bg-red-500'
                }`}
            />
            <span className="text-xs text-gray-500">
              {isConnected ? 'Ïò®ÎùºÏù∏' : 'Ïò§ÌîÑÎùºÏù∏'}
            </span>
          </div>
        </header>

        <main className="flex-1 overflow-y-auto px-4 py-6 space-y-4">
          {isLoading ? (
              <div className="text-center text-gray-500">Î©îÏãúÏßÄÎ•º Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
          ) : messages.length === 0 ? (
              <div className="text-center text-gray-500">
                ÏïÑÏßÅ Î©îÏãúÏßÄÍ∞Ä ÏóÜÏäµÎãàÎã§.<br/>
                <span className="text-xs">Ï≤´ Î©îÏãúÏßÄÎ•º Î≥¥ÎÇ¥Î≥¥ÏÑ∏Ïöî!</span>
              </div>
          ) : (
              messages.map((msg) => {
                const isMine = msg.userSignId === currentUserId;

                return (
                    <div
                        key={msg.chatId}
                        className={`flex ${isMine ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                          className={`flex flex-col max-w-[80%] sm:max-w-md ${
                              isMine ? 'items-end' : 'items-start'
                          }`}
                      >
                        {!isMine && (
                            <span className="text-xs font-medium text-gray-600 mb-1 px-1">
                              {msg.name || msg.userSignId}
                            </span>
                        )}

                        <div
                            className={`px-4 py-3 rounded-2xl shadow-sm break-words ${
                                isMine
                                    ? 'bg-blue-600 text-white rounded-br-none'
                                    : 'bg-white text-gray-900 rounded-bl-none border border-gray-200'
                            }`}
                        >
                          {msg.content}
                        </div>

                        <span className="text-xs text-gray-400 mt-1 px-1">
                          {formatTime(msg.createdAt)}
                        </span>
                      </div>
                    </div>
                );
              })
          )}
          <div ref={messagesEndRef} />
        </main>

        <footer className="border-t border-gray-200 bg-white p-4">
          <div className="flex items-center gap-3 max-w-4xl mx-auto">
            <input
                ref={inputRef}
                type="text"
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder={isConnected ? "Î©îÏãúÏßÄÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî..." : "Ïó∞Í≤∞ Ï§ë..."}
                disabled={!isConnected}
                className="flex-1 px-5 py-3 text-base border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:bg-gray-100 disabled:cursor-not-allowed transition"
                autoFocus
            />
            <button
                onClick={handleSendMessage}
                disabled={!isConnected || !inputMessage.trim()}
                className="p-3 bg-blue-600 text-white rounded-full hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition shadow-lg"
                aria-label="Ï†ÑÏÜ°"
            >
              <Send className="w-5 h-5" />
            </button>
          </div>

          {process.env.NODE_ENV === 'development' && (
              <div className="mt-2 p-2 bg-gray-100 rounded text-xs">
                <div>üîå Ïó∞Í≤∞: {isConnected ? '‚úÖ' : '‚ùå'}</div>
                <div>üè† Î∞©: {roomId}</div>
                <div>üë§ ÏÇ¨Ïö©Ïûê: {currentUserId}</div>
                <div>üì° Íµ¨ÎèÖ: /topic/chat.room.{roomId}</div>
              </div>
          )}
        </footer>
      </div>
  );
}